.button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 56px;

  text-decoration: none;

  color: $colorBlue;
  border: 2px solid $colorBlue;
  border-radius: 8px;

  font-weight: 700;
  font-size: 16px;
  line-height: 14px;

  transition: all 0.3s;

  &:hover {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
    color: #fff;
  }

  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 8px 14px rgba(0, 83, 181, 0.48);
  }
}
